    }
    },
    methods:{

  
      gotoroute(pathname){
        this.$router.push(pathname)

        
      },  
      appear(content, property) {
          this[property] = "";
          clearTimeout(this[property + "Timer"]);
          let _this = this;
          let speed =100;
          let count = 1;
          function changeContent() {
            _this[property] = content.substring(0, count);
            count++;
            if (count <= content.length) {
              speed -= 1;
              if (speed < 5) speed = 5;
    
              _this[property + "Timer"] = setTimeout(changeContent, speed);
            }
          }
    
          changeContent();
        },
        handleScroll() {
        const scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
  
        console.log(scrollTop,'scrollTop');
        if(scrollTop > 678){
          if (this.agiTexts1 === "") {
          this.appear(">", "agiTexts1");
          this.appear(">", "agiTexts2");
          this.appear(">", "agiTexts3");
          this.appear("> ", "agiTexts4");
          this.appear("> ", "agiTexts5");
          // this.appear(" Founder  ", "agiTexts6");
          this.appear(">", "agiTexts7");
  
  
          clearInterval(this.Subforumtimess2)
          this.Subforumtimess2 = window.setTimeout(()=>{
            this.Intervaltimes2 =  window.setInterval(() => {
                console.log(this.Subforumtimess2,'Subforumtimess');
                this.Subforum1 = !this.Subforum1;
            }, 500);
          },1000)
          }
  
  
        }
   },
    },
    mounted(){
      window.addEventListener('scroll', this.handleScroll, true);
      // this.appear("", "SubforumText");
